<script>
export default {
   name: 'contact-us',
   components: {

   },
   mixins: [],
   props: {

   },
   data() {
      return {
         submitStatus:'',
         commentsform: {
            typeOfFeedback:'',
            eventDateAndTime:'',
            comments:''
         },
         personalForm:{
            name:'',
            email:'',
            phoneNumber:''
         },
         rules:{
            typeOfFeedback:[{ required: true, message: '請選擇反映事項', trigger: 'change' }],
            eventDateAndTime:[{ required: true, message: '請選擇日期時間', trigger: 'change' }],
            comments:[{ required: true, message: '請输入你的建議', trigger: 'change' }],
            name:[{ required: true, message: '請输入你的姓名', trigger: 'change' }],
            email:[{ required: true, message: '請输入你的電郵', trigger: 'change' }],
            phoneNumber:[{ required: true, message: '請输入你的電話', trigger: 'change' }],
         }
      }
   },
   computed: {
      internationalDict() {
         return this.$store.getters.getInternationalDict
      }
   },
   watch: {

   },
   mounted() {

   },
   methods: {
      submitHandler(){
         let validDone = 0;
         this.$refs.commentsform.validate((valid)=>{
            if(valid){
               validDone++
            }
         })
         this.$refs.personalForm.validate((valid)=>{
            if(valid){
               validDone++
            }
         })
         if(validDone >= 2){
            this.$message({
               type:'success',
               message:'成功提交'
            })
            this.$refs.commentsform.resetFields()
            this.$refs.personalForm.resetFields()
         }
      }
   }
};
</script>
